body {
    padding: none;
    margin: 0;
}

.container {
	width: 100%;
	top: 0;
	margin: 0;
	position: fixed;
	z-index: 2;
}

.sidebar-container {
	background-color: rgba(26, 26, 26, 0.45);
    /* background-image: linear-gradient(to bottom right, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0)); */
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
    box-shadow: 5px 0px 10px rgba(30,30,30,0.5);
	height: 100vh;
	padding: 0;
	margin: 0;
	width: 15rem;
	position:fixed;
	transition: -webkit-transform 0.2s linear;
	transition: transform 0.2s linear;
	transition: transform 0.2s linear, -webkit-transform 0.2s linear;
	font-family: 'Montserrat', 'sans-serif';
	font-weight: 400;
	font-size: 1rem;
	text-align: center;
	color: #f5f5f5;
}

.hidden {
	-webkit-transform: translate(-15rem);
	        transform: translate(-15rem);
	box-shadow: none;
}

.visible {
	-webkit-transform: translate(0);
	        transform: translate(0);
}

@media only screen and (min-width: 0px) and (max-width: 600px) {
	.sidebar-container {
		width:100vw;
		text-align: center;
	}

	.hidden {
		-webkit-transform: translate(-100vw);
		        transform: translate(-100vw);
		box-shadow: none;
	}
}

.icon {
	margin: 0;
	float: left;
	padding-left: 0.5rem;
	padding-top: 0.5rem;
	cursor: pointer;
	color: rgb(20, 144, 226);
}

.close{
	float: right;
	padding-left: 0;
	padding-right:0.5rem;
	color: rgb(20, 144, 226);
	position: static;
}

.sidebar-container ul {
	list-style: none;
	display: block;
	position: static;
	width: inherit;
	margin:0;
	padding:0;
}

.sidebar-container ul li {
	list-style: none;
	display: block;
	padding: 6% 0 6% 0;
	width: 100%;
	overflow: hidden;
}

.sidebar-container ul li a {
	text-decoration: none;
	margin:0;
	padding: 0;
	top: 0;
	color:inherit;
}

.sidebar-container ul li a:hover {
	color: rgb(20, 144, 226);
	font-weight: 600;
}

.sidebar-container ul li a:active {
	color:rgb(20, 144, 226, 0.55);
}

.MuiTimeline-root {
    font-family: 'Montserrat', 'sans-serif';
    font-weight: 200;
}

.MuiTimeline-root * {
    font-family: 'Montserrat', 'sans-serif';
    font-weight: 300;
}

.MuiTimelineDot-root {
    border-color: rgb(20, 144, 226) !important;
    color:rgb(20, 144, 226) !important;
    background-color: rgb(20, 144, 226) !important;
    box-shadow: none !important;
}

.MuiTimelineConnector-root {
    background-color:rgb(15, 15, 15, 0.75) !important;
    width: 1px !important;
}

.timeline .job-title {
    font-weight: 500;
}

.timeline .org-name {
    font-style: italic;
    font-weight: 400;
    font-size: 1rem;
}

.timeline .job-desc {
    font-size: 1rem;
}

.outer-container {
    top:0;
    width: 85%;
    height: 500px;
    margin: 2rem auto;
    display:block;
}

.cardTab {
    width:50px;
    height:15px;
    margin:0;
    float:right;
    background-color: #1a1a1a;
}

.cardContainer {
    margin:0;
    padding:10px;
    width: 100%;
    background-color: #f5f5f5;
    display: flex;
}

.cardGraphic, .cardInfo {
    flex: 1 1;
    width: 50%;
    float: left;
}

.cardGraphic img {
    height: 80px;
    width: 80px;
}


/* Highest level container */
.body-container {
    top:0;
}

/* Body section containers */
.body-section {
    background-color: #f5f5f5;
    margin:0;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    overflow: hidden;
    display:flex;
    flex-direction: column;
}

.home-container {
    /* background-image: linear-gradient(to bottom right, #ec8734,#e92646,#e0d420); */
    background-image: url(/static/media/background-b-w.ca105f24.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    font-family: 'Montserrat', 'sans-serif';
}

.about-container {
    background-color: #f5f5f5;
    padding: 50px;
}

.skills-container {
    background: linear-gradient(to right bottom, rgb(30, 30, 30, 0.75), rgb(75, 166, 228));
}

/* .home-container section */

.topline-container {
    display:block;
    position: relative;
    margin: 0.5rem auto 0 auto;
    text-align: center;
}

.topline-container p {
    padding: 0;
    margin: 0 2rem 0 2rem;
    display: inline;
    font-weight: 500;
    font-size: 1.2rem;
    color: #1a1a1a;
}

.topline-container .break {
    font-weight: 100;
    font-size: 25px;
}

@media only screen and (min-width: 700px) and (max-width: 950px){
    .topline-container p {
        margin: 0 0.1rem 0 0.1rem;
    }
}

@media only screen and (max-width: 699px) {
    .topline-container p {
        display: none;
    }

    .about-container {
        padding: 5px;
    }
}

.img-container {
    display:block;
    position: relative;
    top: 200px;
    margin: 0 auto;
    z-index:1;
}

.home-profile {
    width: 200px;
}

.blurb-container {
    top: 150px;
    margin: 0 auto;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    position:relative;
    background-image: linear-gradient(to bottom, rgba(150, 150, 150, 0.25), rgba(150, 150, 150, 0.65));
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    box-shadow: 0 5px 12px 3px rgba(30, 30, 30, 0.35);
    border-radius: 15px;
    color: rgb(235, 229, 229);
    font-size: 40px;
    padding:2rem 3rem 1rem 3rem;
    font-weight:100;
    text-align: center;
}

.blurb-container p {
    margin: 0;
    padding: 0;
}

.name-para {
    font-weight: 800;
    font-size: 60px;
    color: rgb(75, 166, 228);
    /* color: #f5f5f5; */
    /* color: #1a1a1a; */
}

/* .about-container section */

.about-statement {
    margin: 0 auto;
    position: relative;
    width: 85%;
}

.about-statement p {
    text-align: center;
    margin:0;
    padding:0;
    font-family: 'Montserrat', 'sans-serif';
    font-weight: 300;
    font-size: 1.1rem;
    line-height: 1.75rem;
}

.jobTimeline {
    margin-top: 5rem;
}

.jobTimeline p {
    margin: 0;
    padding: 0;
}

